import component from './main';

const createConfItems = () => {
    const fieldConf = {
        field: 'field',
        fieldName: '',
        cType: 'input',
        config: {
            isRequire: true,
            placeholder: ''
        }
    }

    const defaultValueConf = {
        field: 'defaultValue',
        fieldName: '',
        cType: 'input',
        config: {
            isRequire: false,
            placeholder: ''
        }
    };

    const placeholderConf = {
        field: 'placeholder',
        fieldName: '',
        cType: 'input',
        config: {
            isRequire: false,
            placeholder: ''
        }
    }

    const requireConf = {
        field: 'isRequire',
        fieldName: '',
        cType: 'switch',
        config: {
            defaultValue: false,
            options: [true, false]
        }
    }

    return [fieldConf, defaultValueConf, placeholderConf, requireConf];
}

const createDefaultConf = () => {
    return {
        field: '',
        defaultValue: '',
        placeholder: '',
        isRequire: false
    }
}


export default {
    component,
    createDefaultConf,
    createConfItems
};