import component from './main';

const createConfItems = () => {
    const fieldConf = {
        field: 'field',
        fieldName: '',
        cType: 'input',
        config: {
            isRequire: true,
            placeholder: ''
        }
    }

    const defaultValueConf = {
        field: 'defaultValue',
        fieldName: '',
        cType: 'select',
        config: {
            isRequire: false,
            multiple: true,
            placeholder: '',
            options: []
        }
    };

    const optionsConf = {
        field: 'options',
        fieldName: '',
        cType: 'inputlist',
        config: {
            isRequire: true,
            placeholder: ''
        },
        listeners: {
            change: (v) => {
                defaultValueConf.config.options = v;
            }
        }
    }

    const requireConf = {
        field: 'isRequire',
        fieldName: '',
        cType: 'switch',
        config: {
            defaultValue: false,
            options: [true, false]
        }
    }

    return [fieldConf, defaultValueConf, optionsConf, requireConf];
}

const createDefaultConf = () => {
    return {
        field: '',
        defaultValue: [],
        options: ['', ''],
        isRequire: false
    }
}


export default {
    component,
    createDefaultConf,
    createConfItems
};