import component from './main';

const createConfItems = () => {
    const fieldConf = {
        field: 'field',
        fieldName: '',
        cType: 'input',
        config: {
            isRequire: true,
            placeholder: ''
        }
    }

    const defaultValueConf = {
        field: 'defaultValue',
        fieldName: '',
        cType: 'select',
        config: {
            isRequire: false,
            options: ['', '']
        }
    };

    return [fieldConf, defaultValueConf];
}

const createDefaultConf = () => {
    return {
        field: '',
        defaultValue: '',
        options: ['', '']
    }
}


export default {
    component,
    createDefaultConf,
    createConfItems
};